import app from './_firebase';

import {getFirestore} from 'firebase/firestore'

export const addGroup = async (data) => {
  try {
    var checkGroup = await app.firestore().collection('groups').where('hash_id', '==', data.hash_id).get();
    if (checkGroup.docs.length > 0) {
      return 'error';
    } else {
      var result = await app.firestore().collection('groups').add(data);
      return 'success';
    }
  } catch (err) {
    console.log(err);
  }
};

export const getClassApi = async (id) => {
  try {
    let querySnapshot = await app.firestore().collection('classes').doc(id).get();
    const classes = querySnapshot.data();
    classes.id = id;
    return classes;
  } catch (err) {
    console.log(err);
  }
};

export const updateClass = async (data) => {
  try {
    console.log(data);
    let id = data.id;
    let result = await app.firestore().collection('classes').doc(id).update(data);

    return result;
  } catch (err) {
    console.log(err);
  }
};

export const listGroup = async (userId) => {
  try {
    let querySnapshot = await app.firestore().collection('groups').where('created_by', '==', userId).get();
    if (querySnapshot.docs) {
      const groups = querySnapshot.docs.map((doc) => {
        return {
          id: doc.id,
          ...doc.data()
        };
      });
      return groups;
    }
  } catch (err) {
    console.log(err);
  }
};

export const deletegroups = async (school_id) => {
  try {
    let querySnapshot = await app.firestore().collection('schools').doc(school_id).get();
    const dclasses = await querySnapshot.data().classes;
    let a = [];
    for await (let val of dclasses) {
      const doc = await val.get();
      a[doc.id] = { id: doc.id, ...doc.data() };
    }
    return a;
  } catch (err) {
    console.log(err);
  }
};

export const listMembers = async (groupId) => {
  try {
    let querySnapshot = await app.firestore().collection('groups').doc(groupId).collection('group_members').get();
    const members = querySnapshot.docs.map((doc) => {
      return { id: doc.id, ...doc.data() }
    })
    return members
  } catch (err) {
    console.log(err);
  }
};

